<<template>
    <transition name="slide-fade">
        <nav v-show="ok" class="sider-bar">
            <ul>
                <li><a href="#/login"><i class="fa fa-sign-in fa-lg"></i></a></li>
                <li><a href="#/all"><i class="fa fa-calendar-o fa-lg"></i></a></li>
                <li><a href="#/done"><i class="fa fa-calendar-check-o fa-lg"></i></a></li>
                <li><a href="#/doing"><i class="fa fa-calendar-times-o fa-lg"></i></a></li>
                <li><a href="#/backup"><i class="fa fa-cloud-upload fa-lg"></i></a></li>
                <li><a href="#/about"><i class="fa fa-info fa-lg"></i></a></li>         
            </ul>
        </nav>
    </transition>
</template>

<<script>
    import bus from "../js/eventBus.js"

    export default {
        data() {
            return {
                ok: false
            }
        },
        mounted() {
            bus.$on("toggleNav", msg => {
                this.ok = msg;
            })
        }
    }
</script>

<style lang="scss">
    @import "../scss/nav.scss";
</style>



